import { UserSession } from './../core/sessions';
import { InfiniteScroll } from './../core/constants';
import { LocalStorageService } from './../core/service/local-storage.service';
import { HttpUserAuthUtilsService } from './../core/http_wrapper/http-userauth-utils.service';
import { Component, OnInit } from '@angular/core';
import { HttpSuccesFailureResponse } from '../core/http_wrapper/http_wrapper_response';
import { Constants } from '../core/constants';
import { NotificationsService } from './notifications.service';
import { NotificationResponseData } from '../core/models/notificationResponseList';
import { NotificationTypes } from '../core/models/notificationType.enum';
@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.css']
})
export class NotificationsComponent implements OnInit, HttpSuccesFailureResponse {

  public notifications: Array<NotificationResponseData> = [];
  public cancelledNotifications: Array<NotificationResponseData> = [];
  public addedNotifications: Array<NotificationResponseData> = [];
  public favoriteNotifications: Array<NotificationResponseData> = [];

  private page: number = -1;
  private loadMoreData: boolean = true;
  isLoaded: boolean = false;
  notificationListLoading:boolean=false;
  public throttle = InfiniteScroll.throttle;
  public scrollDistance = InfiniteScroll.scrollDistance;
  public scrollUpDistance = InfiniteScroll.scrollUpDistance;

  constructor(
    private http_user: HttpUserAuthUtilsService,
    private _notificationService: NotificationsService
  ) { }

  ngOnInit() {
    if (LocalStorageService.getIsLoggedIn() && LocalStorageService.getUserId() !== 0) {
      this.notificationListLoading = true;
      this.http_user.getNotifications(this, ++this.page, Constants.pageSize);
         }
  }

  onScrollDown(type: string) {
    if (this.loadMoreData && !this.notificationListLoading) {
      this.http_user.getNotifications(this, ++this.page,Constants.pageSize);
    }
  }
  onScroll() {

  }

  onSuccess(type: string, responseData: any) { 
    if (type == Constants.getNotificationsApi) {
      this.notificationListLoading=false;
        if (this.notifications.length == 0) {
          this.notifications = this._notificationService.parseNotifications(responseData.data);
        }
        else {
          this.notifications.push.apply(this.notifications, this._notificationService.parseNotifications(responseData.data));
        }

        if (responseData.data.length === 0) {
          this.loadMoreData = false;
        }
    }
  }

  onFailure(type: string, error: string) {
    this.notificationListLoading=false;
    console.error(error);
  }

  onNotificationClick(notification: NotificationResponseData) {
		this._notificationService.handleNotificationClick(notification);
	}

}



